import React, { memo } from 'react';

import { Handle } from 'react-flow-renderer';
import { Grid, Paper, Tooltip, IconButton, CardHeader } from '@material-ui/core';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import DeleteIcon from '@material-ui/icons/Delete';
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles((theme) => ({
  title: {
    fontSize: 10,
    height: 10,
    padding: 5

  },
  heading: {
    fontSize: 10,
    fontWeight: 600,
    padding: 5
  },
  subheader: {
    padding: 5
  },
  desc: {
    fontSize: 7
  },
  deleteIcon: {
    height: 10
  },
  root: {
    maxWidth: 345,
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
}));
export default memo(({ data }) => {
  const classes = useStyles();
  return (
    <>
      <Handle
        type="target"
        position="top"
        style={{ background: 'blue' }}
        onConnect={(params) => console.log('handle onConnect', params)}
      />
      <Card className={classes.root}>
        <CardHeader className={classes.title}
          action={
            <IconButton aria-label="delete" >
              <DeleteIcon className={classes.deleteIcon} />
            </IconButton>
          }
        />
        <CardActionArea>
          <CardContent>
            <Typography variant="body2" className={classes.heading} color="textSecondary" component="p">
              {data.name}
            </Typography>
            <Typography variant="body2" className={classes.desc} color="textSecondary" component="p">
              {data.description}
            </Typography>
          </CardContent>

        </CardActionArea>

      </Card>
      <Handle
        type="source"
        position="left"
        id="a"
        style={{ background: 'blue' }}
      />
      <Handle
        type="source"
        position="right"
        id="b"
        style={{ background: 'blue' }}
      />
    </>
  );
});